.searchArea {
  width: 100vw;
  height: 100vh;
  background-image: url(../images/blueBg.svg);
  background-size: cover;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding: 12.5vh 15vw 0 15vw;

  & h1 {
    font-weight: 700;
    font-size: 6.4rem;
    line-height: 7.4rem;
    display: flex;
    align-items: center;
    text-align: center;
    color: var(--white);
  }

  & p {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 2.8rem;
    display: flex;
    align-items: center;
    text-align: center;
    color: var(--white);
    margin-bottom: 2.5vh;
  }

  & label {
    display: flex;
    justify-content: center;
    width: 80%;
    position: relative;

    & i {
      position: absolute;
      top: 50%;
      font-size: 3rem;
      color: var(--orange);
      transform: translate(0, -50%);
    }
  }

  & input {
    border: none;
    border-radius: 50px;
    margin: auto;
    width: 100%;
    font-size: 1.8rem;
    font-weight: 700;
    padding: 24px 13% 24px 12%;
  }
}

.magnifierIcon {
  left: 5%;
}

.arrowIcon {
  left: 90%;
  cursor: pointer;
}

.answersArea {
  width: 100vw;
  padding: 0 15vw;

  & h1 {
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 55px;
    display: flex;
    justify-content: center;
    text-align: center;
    color: var(--orange);
    margin: 10vh 0 5vh;
  }

  & h2 {
    font-weight: 700;
    font-size: 3.2rem;
    line-height: 37px;
    display: flex;
    align-items: center;
    color: var(--orange);
  }

  & section {
    margin-bottom: 5vh;
  }
}

.questionFormArea {
  width: 100vw;
  padding: 0 25vw;
  margin-bottom: 20vh;

  & h1 {
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 5.5rem;
    display: flex;
    align-items: center;
    text-align: center;
    color: var(--orange);
    margin-bottom: 5rem;
  }

  & form {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;

    & input {
      width: 100%;
      border-radius: 1.5rem;
      border: 1px solid var(--gray);
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 2.1rem;
      display: flex;
      padding: 20px;
      align-items: center;
      color: var(--gray);
      margin-bottom: 2rem;
    }

    & textarea {
      width: 100%;
      border-radius: 1.5rem;
      border: 1px solid var(--gray);
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 2.1rem;
      display: flex;
      padding: 20px;
      align-items: center;
      color: var(--gray);
      margin-bottom: 2rem;
      resize: none;
    }

    & button {
      width: 50%;
      border-radius: 5rem;
      height: 5rem;
      border: none;
      background-color: var(--orange);
      color: var(--white);
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 150%;
      cursor: pointer;
    }

    select {
      width: 100%;
      border-radius: 1.5rem;
      border: 1px solid var(--gray);
      background-color: var(--white);
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 2.1rem;
      padding: 20px;
      color: var(--gray);
      margin-bottom: 2rem;
    }
  }
}

@media only screen and (max-width: 1024px) {

  .searchArea {
    padding: 12.5vh 5vw 0 5vw;

    h1 {
      font-size: 5.4rem;
    }

    label {
      width: 100%;
      
      input {
        padding: 16px 16% 16px 16%;
      }
    }
  }

  .answersArea {
   padding: 0 5vw;
  }

  .questionFormArea {
    padding: 0 5vw;


  }
}
