.firstSection {
  display: flex;
  flex-flow: column nowrap;
  width: 100vw;
  padding: 180px 10vw 0 15vw;
  align-items: center;
  margin: 0 auto;
  background: linear-gradient(180deg, #2dc6ea 0%, #2ba9c8 100%);
  mix-blend-mode: normal;

  & .firstArticle {
    display: grid;
    grid-template-columns: 40% 60%;
    align-items: center;
    padding-right: 10vw;
  }

  & .secondArticle {
    display: grid;
    grid-template-columns: 60% 40%;
    align-items: center;
    overflow-x: visible;

    & img {
      height: 80%;
      max-width: none;
      margin-left: -4vw;
    }

    & button {
      margin-top: 32px;
      border: 2px solid var(--white);
      border-radius: 30px;
      background-color: transparent;
      color: var(--white);
      padding: 8px 16px;
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 400;
      font-size: 1.8rem;
      line-height: 150%;
    }
  }
}

.slogan {
  font-family: 'Ubuntu';
  width: fit-content;
  font-style: normal;
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 130%;
  letter-spacing: 0.05em;
  padding: .5rem 2rem;
  margin-bottom: 16px;
  border-radius: 3rem;
  background-color: var(--white);
  color: var(--blue);
}

.spotlight {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 700;
  font-size: 6.4rem;
  line-height: 74px;
  text-align: start;
  margin-bottom: 32px;
  color: var(--white);
}

.lowProfile {
  font-family: 'Ubuntu';
  font-style: normal;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 130%;
  letter-spacing: 0.05em;
  color: var(--white);
}

.tip {
  padding-left: 5vw;
  margin: 0 auto;
}

.secondSection {
  display: flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding-top: 72px;
}

.secondSectionSlide {
  display: grid;
  padding: 0 17.5vw;
  grid-template-columns: 40% 60%;
  align-items: center;
  width: 100vw;

  & h1 {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 55px;
    text-decoration: underline;
    color: var(--orange);
    margin-bottom: 20px;
  }

  & p {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 2.4rem;
    line-height: 130%;
    letter-spacing: 0.05rem;
    color: var(--gray);
  }
}

.thirdSection {
  position: relative;

  & article {
    position: absolute;
    z-index: 5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;

    & h1 {
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 700;
      font-size: 4rem;
      line-height: 55px;
      color: var(--white);
      margin-bottom: 32px;
    }

    & p {
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 400;
      font-size: 2.4rem;
      line-height: 130%;
      text-align: center;
      letter-spacing: 0.05em;
      margin-bottom: 48px;
      color: var(--white);
    }

    & div {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 20px;

      & button {
        background-color: transparent;
        border: 2px solid var(--white);
        border-radius: 30px;
        color: var(--white);
        padding: 12px 32px;
      }
    }
  }
}

.thirdSectionBackground {
  height: fit-content;
  background-image: url('../images/home/section3.jpg');
  height: 70vh;
  max-height: 700px;
}

.thirdSectionUpperCover {
  background: linear-gradient(180deg, #ff8f29d9 0%, #e46d00d9 100%);
  mix-blend-mode: normal;
  position: absolute;
  width: 100vw;
  height: 70vh;
  max-height: 700px;
  z-index: 2;
}

.thirdSectionLowerCover {
  background: linear-gradient(
    180deg,
    var(--orange) 0%,
    var(--dark-orange) 100%
  );
  mix-blend-mode: multiply;
  position: absolute;
  width: 100vw;
  height: 70vh;
  max-height: 700px;
  z-index: 1;
}

.fourthSection {
  & div {
    position: relative;
    background-image: url('../images/home/section4.jpg');
    width: 100vw;
    height: 70vh;
    max-height: 700px;

    & canvas {
      background: var(--black);
      opacity: 0.42;
      width: 100vw;
      height: 70vh;
      max-height: 700px;
    }
  }
}

.fourthSectionCard {
  position: absolute;
  top: 50%;
  left: 15vw;
  transform: translate(0, -50%);
  background: var(--white);
  box-shadow: 0px 0px 30px 3px rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  width: 35em;
  padding: 6em 3em;
  height: 45em;
  z-index: 5;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;

  & h1 {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 3.6rem;
    line-height: 41px;
    display: flex;
    align-items: center;
    color: var(--blue);
  }

  & p {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 130%;
    letter-spacing: 0.05em;
    color: var(--gray);
  }

  & button {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 150%;
    color: var(--gray);
    background-color: transparent;
    border: 2px solid var(--gray);
    border-radius: 30px;
    padding: 6px 32px;
  }
}

.fifthSection {
  width: 100vw;
  padding: 0 15vw;

  & div {
    & h1 {
      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 700;
      font-size: 4.8rem;
      line-height: 55px;
      display: flex;
      align-items: center;
      margin-top: 4vh;
      color: var(--orange);
    }
  }
}

@media only screen and (max-width: 1366px) {
  .firstSection {
    padding: 180px 10vw 0 5vw;
    .firstArticle {
      padding-right: 0;
    }
    .secondArticle {
      grid-template-columns: 50% 50%;

      img {
        max-width: 180%;
        margin-left: 0;
      }
    }
  }
}

@media only screen and (max-width: 1024px) {
  .firstSection {
    
    .firstArticle {
      grid-template-columns: 100%;
      grid-template-rows: 50% 50%;
      align-items: flex-start;

      img {
        height: 100%;
        margin: 0 auto;
      }

      div {
        margin-top: -12vh;
      }
    }

    .secondArticle {
      grid-template-columns: 100%;
      grid-template-rows: 50% 50%;
      align-items: flex-start;
      padding-left: 5vw;

      img {
        height: 70%;
        margin: 0 auto;
        grid-row: 1;
      }

      div {
        margin-top: -15vh;
      }
    }

  }

  .secondSection {
    padding-bottom: 72px;
  }

  .thirdSection {
    h1 {
      span {
        background-color: var(--white);
        color: var(--orange);
        border-radius: 8px;
      }
    }
  }

  .fourthSection {
    div {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .fourthSectionCard {
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  .fifthSection {
    padding: 0 5vw;
  }
}