.container {
  display: flex;
  flex-flow: column nowrap;
  align-self: flex-start;
  z-index: 100;
  height: 100vh;
  width: 65vw;
  margin-right: -5vw;
  font-size: 12px;
  background-color: var(--gray);
  transition: 0.4s ease;

  &.inactive {
    margin-right: -160vw;
  }

  i {
    font-size: 48px;
    color: var(--white);
    align-self: flex-end;
    padding: 5vw 5vw 0 0;
    transition: 0.2s ease;

    &:hover {
      font-size: 52px;
    }

    &:active {
      font-size: 44px;
    }
  }
}

.nav {
  padding-top: 6em;
  flex-flow: column;
  gap: 3.75em !important;
  a {
    font-size: 1.5em;
    color: var(--white) !important;
  }
}

.levMobile {
  padding: .5em 1em;
  font-size: 1.25em !important;
  border: 2px solid var(--white)!important;
}
