footer {
  font-family: 'Ubuntu';
  background-color: black;
  color: white;
  height: 50rem;
  padding: 8rem 15vw 4rem 15vw;
  display: grid;
  grid-template-rows: 60% 40%;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas: 'logo contatos app seja'
                       'copyright . . social';
  font-size: 1.8rem;
  
  & div {
    width: 100%;
    
    & p {
      &:not(:last-child) {
        margin-bottom: 4rem;
      }
    }
    
    & i {
      font-size: 3rem;
      cursor: pointer;

      &:hover {
        color: var(--orange);
      }

      &:not(:last-child) {
        margin-right: 3rem;
      }      
    }
  }
}

.footerSectionTitle {
  color: var(--orange);
  margin-bottom: 2.3rem;
}

.storeSection {
  display: flex;
  flex-direction: column;

  & img {
    width: 14.6rem;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }
}

.lev {
  border: 0.2rem solid var(--white);
  border-radius: 3rem;
  padding: 0.8rem 3.2rem;

  &:hover {
    color: var(--orange);
    background-color: white;
  }
}

.logo {
  grid-area: logo;
}

.contatos {
  grid-area: contatos;
}

.app {
  grid-area: app;
}

.seja {
  grid-area: seja;
}

.social {
  grid-area: social;
}

.copyright {
  grid-area: copyright;
}

@media only screen and (max-width: 1366px) {

  footer {
    padding: 8rem 5vw 4rem 5vw;
  }

}

@media only screen and (max-width: 1024px) {

  footer {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas: 'logo contatos seja'
                          'copyright app social';
  }

}