.container {
  .mainContent {
    width: 100vw;
    height: 100vh;
    padding: 0 0 0 15vw;
    background-image: url(../images/blueBg.svg);
    background-size: cover;
    display: grid;
    grid-template-columns: 39vw 33vw;
    grid-template-rows: 100%;
    align-items: flex-end;

    & article {
      align-self: center;

      & h1 {
        font-weight: 700;
        font-size: 4rem;
        line-height: 5rem;
        color: var(--white);
      }

      & h2 {
        font-weight: 700;
        font-size: 2.4rem;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: var(--blue);
        background-color: var(--white);
        border-radius: 2rem;
        padding: 0 3rem;
        width: fit-content;

      }

      & p {
        font-weight: 400;
        font-size: 2.4rem;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: var(--white);
      }

      & button {
        font-weight: 700;
        font-size: 1.8rem;
        line-height: 150%;
        margin-top: 3rem;
        padding: 0.8rem 3rem;
        border: none;
        border-radius: 3rem;
        background-color: transparent;
        color: var(--white);
        border: 2px solid var(--white);
        cursor: pointer;
        transition: all 0.3s ease;

        &:active {
          transform: scale(0.97);
        }
      }
    }
  }
}

.tip {
  position: absolute;
  top: 97%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.benefitsTitle {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  margin-top: 20vh;
  margin-bottom: 10vh;

  & h1 {
    font-weight: 700;
    font-size: 4.8rem;
    line-height: 5.5rem;
    text-align: center;
    color: var(--orange);
  }
}

.cardsContainer {
  width: 100vw;
  padding: 0 15vw;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 20vh;
}

.letterSection {
  position: relative;
  width: 100%;
  padding: 20vh 45vw 30vh 15vw;
  z-index: 0;

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    clip-path: polygon(100% 0, 100% 80%, 50% 100%, 0 80%, 0 0);
    background-image: url(../images/driver/frame.jpg);
    background-size: cover;
  }

  & h1 {
    font-weight: 700;
    font-size: 6.4rem;
    line-height: 7.4rem;
    color: var(--white);
    margin-bottom: 3rem;
  }

  & p {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 130%;
    letter-spacing: 0.05em;
    color: var(--white);
    margin-bottom: 3rem;
  }

  & button {
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 150%;
    color: var(--white);
    padding: 1rem 4rem;
    background-color: transparent;
    border: 2px solid var(--white);
    border-radius: 3rem;
  }

  & img {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 100%;
    left: 50%;
    z-index: 5;
  }
}

.calculatorSection {
  background-image: url(../images/blueBg.svg);
  background-size: cover;
  width: 100vw;
  height: fit-content;
  margin-top: -25vh;
  padding: 40vh 30vw 10vh;

  & .head {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;

    & h1 {
      font-weight: 700;
      font-size: 4.8rem;
      line-height: 5.5rem;
      color: var(--white);
      margin-bottom: 2rem;
    }

    & p {
      font-weight: 400;
      font-size: 2.4rem;
      line-height: 130%;
      text-align: center;
      letter-spacing: 0.05em;
      text-align: center;
      color: var(--white);
    }
  }
}

.signinSection {
  width: 100vw;
  padding: 15vh 15vw;
  display: grid;
  grid-template-columns: 50% 50%;
  background: linear-gradient(180deg, #ff8f29 0%, #e46d00 100%);
  gap: 2rem;
  margin-top: -1px;

  & article {
    display: flex;
    flex-flow: column nowrap;

    & h1 {
      font-weight: 700;
      font-size: 4.8rem;
      line-height: 5.5rem;
      color: var(--white);
      margin-bottom: 2rem;
    }

    & p {
      font-weight: 400;
      font-size: 2.4rem;
      line-height: 130%;
      letter-spacing: 0.05em;
      color: var(--white);
    }
  }

  & form {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    gap: 1rem;

    & input {
      width: 100%;
      padding: 2rem 3rem;
      border: none;
      border-radius: 1rem;
      color: var(--gray);
    }

    & button {
      margin-top: 1rem;
      padding: 1.5rem 6rem;
      background-color: transparent;
      border: 2px solid var(--white);
      border-radius: 4rem;
      color: var(--white);
      font-weight: 500;
      font-size: 2.4rem;
      line-height: 2.8rem;
    }
  }
}

@media only screen and (max-width: 1024px) {
  .container {
    .mainContent {
      position: relative;
      grid-template-columns: 100vw;
      grid-template-rows: 60% 40%;
      padding: 20vw 5vw 0 5vw;
      padding-bottom: 20vh;
      box-sizing: content-box;
      width: 90vw;

      article {
        width: 90vw;
      }

      .personS {
        grid-row: 1;
        margin-bottom: -8vh;
        width: 90%;
        justify-self: start;
      }
    }
  }

  .tip {
    position: absolute;
    top: 97%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .letterSection {
    padding: 20vh 5vw 30vh 5vw;

    &::before {
      clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
    }

    h1 {
      font-size: 3rem;
      line-height: 4rem;
    }
  }

  .calculatorSection {
    padding: 40vh 5vw 10vh;

    .head {
      h1 {
        font-size: 3rem;
        line-height: 4rem;
      }

      p {
        font-size: 2rem;
        line-height: 3rem;
      }
    }
  }
  .signinSection {
    grid-template-columns: 100%;
    grid-template-rows: 60% 40%;
  }
}
