/* Reset CSS */

html {
  box-sizing: border-box;
  font-size: 10px;
  overflow-x: hidden;
}
@media only screen and (max-width: 1024px) {
  html {
    font-size: 8px;
    overflow-x: hidden;
  }
  body {
    overflow-x: hidden;
  }
}

*,
*:before,
*:after {
  font-family: Ubuntu;
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

button {
  cursor: pointer;
}

input:focus {
  outline: var(--gray) solid 2px;
  border: none;
}

a {
  text-decoration: none;
  color: inherit;
  white-space: nowrap;

  &:hover {
    color: var(--dark-orange);
  }
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

/* Paleta de cores */

:root {
  --blue: #2dc6ea;
  --dark-blue: #02a0c6;
  --orange: #ff8f29;
  --dark-orange: #e46d00;
  --gray: #2d2d2d;
  --black: black;
  --drop-shadow: rgba(0, 0, 0, 0.1);
  --white: white;
  --beige: #f9f5f2;
}
