.container {
  background-color: var(--white);
  border-radius: 1.5rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  z-index: 1;
  width: 40rem;
  margin: auto;

  & header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    padding: 2rem;
    gap: 2rem;

    & h1 {
      font-weight: 700;
      font-size: 1.8rem;
      line-height: 2.1rem;
      color: var(--gray);
    }
  }

  & form {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    padding: 0 2rem 2rem 2rem;
    row-gap: 2rem;
    background: inherit;

    & label {
      width: 46%;
      margin: 0 2%;
      flex-grow: 1;
      font-weight: 500;
      font-size: 1.6rem;
      line-height: 1.8rem;
      color: var(--gray);
    }

    & input {
      width: 100%;
      font-weight: 500;
      font-size: 1.6rem;
      line-height: 1.8rem;
      color: var(--gray);
      padding: 2rem 1rem;
      border: 1px solid var(--gray);
      border-radius: 0.5rem;
    }
  }

  & article {
    background-color: var(--blue);
    width: 100%;
    border-radius: 0 0 15px 15px;
    padding: 2rem;

    & p {
      background-color: var(--white);
      border: none;
      border-radius: 1.5rem;
      color: var(--gray);
      padding: 1.5rem 1.5rem;
      font-weight: 500;
      font-size: 1.6rem;
      line-height: 1.8rem;
      width: 100%;
      margin-bottom: 1rem;
      text-align: left;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
