.sectionMain {
  height: 110vh;
  padding: 0 15vw;

  h1 {
    font-size: 6.4rem;
    font-weight: 700;
  }

  & h2 {
    font-size: 4.8rem;
    font-weight: 700;
  }

  & p {
    font-size: 2.4rem;
  }
}

.sectionMain {
  background: linear-gradient(180deg, #2dc6ea 0%, #2ba9c8 100%);
  mix-blend-mode: lighten;
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-items: center;

  & h1 {
    margin: 1.5rem 0 3rem;

    & ~ p {
      margin-bottom: 3rem;
    }
  }

  & > img {
    width: 80rem;
  }
}
.storeButtons {
  & a, img {
    width: 18.3rem;
  }

  & a {
    &:first-child {
      margin-right: 2rem;
    }
  }
}

.sectionCards {
  height: fit-content;
  background-color: white;
  padding: 0 15vw;
  gap: 4rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 1fr;
  margin-bottom: 10vh;
}
.secCardsTitle {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10rem;

  img {
    width: 4rem;
    margin-bottom: 2rem;
  }

  h2 {
    font-size: 4rem;
    font-weight: 700;
    color: var(--orange);
  }
}

@media (max-width: 785px) {
  .sectionCard {
    display: flex;
    height: 10rem;
    padding: 0 2rem;
    text-align: center;
  }

  .secCardsTitle {
    text-align: center;
    margin-bottom: 20rem;
  }

  .cards {
    display: flex;
    width: 100%;
  }

  .cards > div {
    display: flex;
    overflow-x: hidden;
  }

  .cards > div input {
    display: none;
  }

  .card {
    background-color: #f1f1f1;
    padding: 2rem;
    border-radius: 2rem;
    min-width: 80%;
    transition: 0.5s;
  }

  .card p {
    font-size: unset;
  }

  .cardHeader img {
    margin-right: 1rem;
  }

  .cardHeader h4 {
    font-size: 3rem;
    text-align: start;
  }

  .cardText {
    text-align: start;
    height: auto;
  }
}
