.slider {
  display: flex;

  &.first {
    & .firstSlide {
      margin-left: 200vw;
    }
  }

  &.second {
    & .firstSlide {
      margin-left: 0vw;
    }
  }

  &.third {
    & .firstSlide {
      margin-left: -200vw;
    }
  }
}

.secondSlide {
  & article {
    margin-top: -13vh;
    margin-left: -7vw;
  }
}

.chevron {
  position: absolute;
  transition: all 0.2s ease;
  font-size: 5rem;
  color: var(--orange);
}

.previous {
  left: 15vw;

  &:active {
    transform: scale(0.8);
  }
}

.next {
  left: 85vw;
  transform: rotate(180deg);
  
  &:active {
    transform: rotate(180deg) scale(0.8);
  }
}

@media only screen and (max-width: 1366px) {
  .secondSlide {
    & article {
      margin-top: -25vh !important;
      margin-left: -7vw;
    }
    // img {
    //   width: 130%;
    // }
  }
  .previous {
    left: 0;
  }
  
  .next {
    left: 90vw;
  }
}