.container {
  display: flex;
  justify-content: space-between;
  padding: 0 15vw;
  align-items: center;
  height: 80px;
  position: fixed;
  top: 0px;
  left: 0;
  width: 100vw;
  transition: all 0.5s ease;
  background-color: transparent;
  z-index: 1000;
  overflow: visible;

  &.active {
    background-color: var(--white);

    & .lev {
      border: 2px solid var(--blue);
    }

    & a {
      color: var(--blue);
    }
  }
  
  canvas {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 90;
    background-color: var(--black);
    opacity: 0.7;
  }

  & img {
    cursor: pointer;
  }

  & nav {
    display: flex;
    gap: 20px;
    align-content: center;
  }

  & a {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 150%;
    color: var(--white);
    text-decoration: none;
    margin: auto;
  }
}

.lev {
  border: 2px solid var(--white);
  border-radius: 30px;
  padding: 8px 32px;
}

.menu {
  font-size: 48px;
  color: var(--white);
  transition: 0.2s ease;

  &:hover {
    font-size: 52px;
  }

  &:active {
    font-size: 48px;
  }

  &.active {
    color: var(--blue);
  }
}

@media only screen and (max-width: 1366px) {
  .container {
    padding: 0 5vw;
  }
}