.slide {
  transition: all 0.5s ease;
  div {
    padding: 0 17.5vw;
    display: flex;
    align-items: center;
    width: 100vw;
    transition: all 0.5s ease;
  }

  & img {
    max-height: 500px;
  }

  & article {
    max-width: 590px;
    margin-top: -5vh;
  }

  & h1 {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 700;
    font-size: 48px;
    line-height: 55px;
    text-decoration: underline;
    color: var(--orange);
    margin-bottom: 20px;
  }

  & p {
    font-family: 'Ubuntu';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.05em;
    color: var(--gray);
  }
}

@media only screen and (max-width: 1366px) {
  .slide {
    padding: 0 10vw;
  }
}

@media only screen and (max-width: 1024px) {
  .slide {
    justify-content: center;
    div {
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: 50% 50%;
      align-items: center;
      justify-items: center;
      box-shadow: 0px 4px 10px 2px rgba(0, 0, 0, 0.25);
      border-radius: 2rem;
      width: 80vw;
      padding: 1rem;
    }

    img {
      grid-row: 2;
      align-self: stretch;
    }
    
    article {
      text-align: center;
      margin-top: 0 !important;
      margin-left: 0 !important;

      h1 {
        font-size: 3rem;
      }

      p {
        font-size: 2rem;
      }
    }
  }
}